@import 'mixins';
@import 'variables';

.paragraphs {
    line-height: 3rem;
    & > * {
        transition: 0.6s;
    }

    .emphasized {
        color: darken($primary-color, 65%);
        font-size: 1.75rem;
    }

    .paragraph-content {
        // transition: 300s linear;
        max-height: 100rem;
        margin-bottom: 2rem;
    }
    .paragraph-content.collapsed {
        // transition: 0.6s linear;
        max-height: 3rem;
        background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0, rgba(0, 0, 0, 0.123) 100%);
        border-bottom: 3px dashed rgba(0, 0, 0, 0.123);
        overflow: hidden;
    }

    a {
        color: rgb(255, 81, 0);
        font-weight: 700;
    }
}